<style scoped lang="stylus">
.detail-msg-container
    display flex
    flex-flow column nowrap
    padding 8px

    .msg
        font-size 20px
        text-align left
    .sub-msg
        margin-top 6px
        color #888
        text-align left
</style>

<template lang="pug">
.detail-msg-container
    .msg(:style='msgStyle') {{detail.value}}
    .sub-msg(:style='subStyle') {{detail.sub}}
</template>

<script>
/**
 * 
 */
export default {
    name: 'DetailMsg',
    data: () => ({
        
    }),
    props: {
        /**
         * 
         * 
         * @property {string} value 
         * @property {string} sub 
         * @property {boolean} done 
         */
        detail: {
            type: Object,
            required: true
        }
    },
    computed: {
        /**
         * 
         */
        msgStyle() {
            return {
                color: this.detail.done ? '#888' : ''
            }
        },
        /**
         * sub 
         * 
         */
        subStyle() {
            return {
                display: this.detail.sub ? 'inline' : 'none'
            }
        }
    },
    methods: { }
}
</script>
